export const sendPsbtTx = async ({
  networkType,
  addressType,
  psbtHex,
}: {
  networkType: BTCNetworkType;
  addressType: BTCNetworkAddressType;
  psbtHex: string;
}) => {
  const params: BtcSignPsbt = {
    networkType: networkType,
    addressType,
    autoFinalized: true,
    psbtHex: psbtHex,
  };
  const signAndPushRes = await btcSignPsbtAndPush(params);
  return signAndPushRes;
  // if (signAndPushRes.message === 'Success') {
  //   handleUpdateSignData(
  //     true,
  //     signAndPushRes.result,
  //     'Transaction sent',
  //     'success',
  //     true
  //   )
  // }
};

export const signPsbt = async ({
  networkType,
  addressType,
  psbtHex,
}: {
  networkType: BTCNetworkType;
  addressType: BTCNetworkAddressType;
  psbtHex: string;
}) => {
  const params: BtcSignPsbt = {
    networkType: networkType,
    addressType,
    autoFinalized: true,
    psbtHex: psbtHex,
  };
  const signRes = await btcSignPsbt(params);
  return signRes;
};

export const getBTCSendHash = async (psbtHex: string) => {
  const psbtHaxTest =
    '';
  // const psbt = bitcoin.Psbt.fromHex(psbtHex)
  const bitcoin = await import('bitcoinjs-lib');
  const psbt = bitcoin.Psbt.fromHex(psbtHaxTest);

  const extractedTx = psbt.extractTransaction();

  const txid = extractedTx.getId();

  return txid;
};
