let globalEndpoints: string[] = [];
let globalTmaUserId = '';
let globalTmaUserKey = '';
export const successCode = 8000;
export const api: {
  index: AxiosInstance | undefined;
  bot: AxiosInstance | undefined;
  auth: AxiosInstance | undefined;
  tomo: AxiosInstance | undefined;
  pk: AxiosInstance | undefined;
  sun: AxiosInstance | undefined;
  init: (endpoints: [string], tmaid: string, tmakey: string) => void;
} = {
  index: undefined,
  bot: undefined,
  tomo: undefined,
  auth: undefined,
  pk: undefined,
  sun: undefined,
  init(endpoints: [string], tmaid, tmakey) {
    const endPoints = {
      bot: endpoints[0] + 'moon/',
      auth: endpoints[0] + 'wind/',
      pk: endpoints[0] + 'rain/',
      sun: endpoints[0] + 'sun/',
      tomo: endpoints[0] + 'sky/api/',
    };
    this.bot = axios.create({ baseURL: endPoints.bot });
    this.auth = axios.create({ baseURL: endPoints.auth });
    this.pk = axios.create({ baseURL: endPoints.pk });
    this.sun = axios.create({ baseURL: endPoints.sun });
    this.tomo = axios.create({
      baseURL: endPoints.tomo,
    });
    globalEndpoints = endpoints;
    globalTmaUserId = tmaid;
    globalTmaUserKey = tmakey;
    [this.index, this.tomo, this.auth, this.bot, this.pk, this.sun].forEach(
      (apiItem: AxiosInstance | undefined) => {
        if (!apiItem) return;
        apiItem.interceptors.request.use(
          config => {
            // if (config.baseURL != endpoints[0] + 'bot-server/') {
            Object.defineProperty(config.headers, 'X-TOMO-TMA-USERID', {
              value: tmaid,
              writable: true,
              enumerable: true,
              configurable: true,
            });

            Object.defineProperty(config.headers, 'X-TOMO-TMA-KEY', {
              value: tmakey,
              writable: true,
              enumerable: true,
              configurable: true,
            });
            // }
            const passKey = getPassKey();

            if (passKey && config.baseURL === endPoints.pk) {
              config.headers.mfa = passKey;
            }
            if (
              passKey &&
              config.baseURL === endPoints.tomo &&
              config.url != 'token/swapList'
            ) {
              config.headers.mfa = passKey;
            }
            const token = getUserToken();

            if (token && config.url != 'token/swapList') {
              config.headers.Authorization = `Bearer ${token}`;
            }

            return config;
          },
          error => {
            console.log('null', error);

            if (error?.response?.status === 401) {
              return Promise.reject(error);
            }
            return null;
          }
        );
      }
    );
  },
};

export class GatewayError extends Error {
  code: number;

  constructor(message: string, code: number = 0) {
    super(message);
    this.name = this.constructor.name;
    this.code = code;
  }
}
export const pkCallback = async (callback: () => Promise<any>) => {
  try {
    const res = await callback();
    if (!res || !res.data) {
      throw new GatewayError('Failed to get data', 500);
    }
    return res.data;
  } catch (err) {
    console.error(err);
    if (err?.response?.data?.message) {
      throw new GatewayError(
        err?.response?.data?.message,
        err?.response?.data?.code
      );
    }
    throw err;
  }
};
