export const tomoCallback = async (callback: () => Promise<any>) => {
  const res = await callback();
  if (res.code != 10000) {
    throw new GatewayError(res.message, res.code);
  }
  return res;
};
export const getPassKey = () => {
  try {
    const token = localStorage.getItem(passkeyKey);
    return token ?? '';
  } catch (e) {
    return null;
  }
};

export const loginApi = async (initData: string) => {
  try {
    const res = await api.bot?.get(`login?${initData}`);
    return res?.data;
  } catch (err) {
    console.error(err);
    if (err.response.status == 401) {
      throw new GatewayError(err.response.data, 401);
    }
    throw err;
  }
};

export const checkLoginByCodeApi = async (session_code: string) => {
  try {
    const res = await api.bot?.get(`session/token`, {
      headers: {
        'session-code': session_code,
      },
    });
    return res?.data;
  } catch (err) {
    console.error(err);
    if (err.response.status == 401) {
      throw new GatewayError('Not logged in', 401);
    }
    throw err;
  }
};

