export const getSwapAllTokensSearch = async (
  queryParameters: {
    content: string;
    chain?: string;
  },
  config: { url: string; tmaid: string; tmakey: string }
): Promise<any> => {
  const ret = await axios.get(`${config.url}wallet/tokens/search`, {
    params: queryParameters,
    headers: {
      'X-TOMO-TMA-USERID': config.tmaid,
      'X-TOMO-TMA-KEY': config.tmakey,
    },
  });
  return ret.data;
};

